*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


header, footer {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Monsterrat', sans-serif;
}


header, h1 {
    font-size: 60px;
}

h2 {
    font-size: 20px;
}

h3 {
    font-size: 30px;
}


h5 {
    font-size: 40px;
    position: absolute;
    display: flex;
    font-family: 'Monsterrat', sans-serif;
    color: rgb(180, 180, 180);
    text-align: center;
}

h6 {
    font-size: 30px;
    position: absolute;
    display: flex;
    font-family: 'Monsterrat', sans-serif;
    color: rgb(97, 97, 97);
    text-align: center;
}

.hidden {
    display: none;
}

.animation {
    height: 100vh;
    /* background-image: linear-gradient(
        to right, 
        #74ebd5 0%, 
        #9face6 100%
        ); */
    background-image: linear-gradient(to right, #434343 0%, black 100%);
    position: relative;
    overflow: hidden;
}

.social-image {
    /* -webkit-box-shadow: 0px 0px 71px -15px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 71px -15px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 71px -15px rgba(0,0,0,0.75); */
    left: 0%;
    top: 0%;
    width: 35vmin;
    position: absolute;
    opacity: 0;
}

.paper-plane {
    /* height: 100px; */
    position: absolute;
    /* top: 15%; */
    left: 0%;
    opacity: 0;
}


.center {
    margin: 0;
    margin-top: 0px;
    /* margin-bottom: 0px; */
    /* width: 50%; */
    /* padding: 10px; */
}

#title-text {
    opacity: 0;
}

#subtitle-text {
    opacity: 0;
}

#helios-viewer {
    width: 50vh; 
    height: 50vh;
    /* align-self: center; */
    z-index: 1;
    opacity: 0;
}


#phone-template{
    /* margin-left: 10%;
    left: 25%; */
    position: absolute;
    z-index: -10;
    opacity: 0;
}

#laptop-template {
    position: absolute;
    z-index: -10;
    left: 0%;
    top: 0%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    opacity: 0;
}

#down-arrow-img{
    /* margin-bottom: 0px; */
    /* width: 50%; */
    /* padding: 10px; */
    width: 75px;
    height: 75px;
    z-index: 2;
    position: relative;
    filter: invert(50%);
    -webkit-filter: invert(50%);
    background-color: #ffffff23;
    border-radius: 10%;
    -webkit-box-shadow: 0px 0px 17px -2px rgba(0, 0, 0, 0.5);
    -moz-box-shadow: 0px 0px 17px -2px rgba(0, 0, 0, 0.5);
    box-shadow: 0px 0px 17px -2px rgba(0, 0, 0, 0.5);
    opacity: 0;
}

#model-background{
    /* background-color: linear-gradient(to left, #434343 0%, black 100%); */
    /* background-color: #43434359; */
    background: radial-gradient(#a7a7a7, #434343);
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    display: flex;
    align-items: stretch;
    border-radius: 10px;
    -webkit-box-shadow: 8px 3px 29px -2px rgba(0,0,0,0.75);
    -moz-box-shadow: 8px 3px 29px -2px rgba(0,0,0,0.75);
    box-shadow: 8px 3px 29px -2px rgba(0,0,0,0.75);
    opacity: 0;
}

#helios-background{
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0%;
    opacity: 0;
}

.center-element {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.center-h{
    margin: 0;
    position: absolute;
    left: 50%;
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
}
.center-v{
    margin: 0;
    position: absolute;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}

.down {
    margin-top: 75vh;
}

#ecomm-header{
    position: absolute;
    display: flex;
    background-color: rgb(116, 116, 116);    
    border-radius: 10px;
    -webkit-box-shadow: 8px 3px 29px -2px rgba(0,0,0,0.75);
    -moz-box-shadow: 8px 3px 29px -2px rgba(0,0,0,0.75);
    box-shadow: 8px 3px 29px -2px rgba(0,0,0,0.75);
}

#ecomm-header-line{
    position: absolute;
    width: 90%;
    height: 5px;
    /* margin-top: 50%; */
    z-index: 1;
    background-color: rgb(44, 44, 44);
    border-radius: 10px;
    
}

#ecomm-body{
    position: absolute;
    display: flex;
    background-color: rgb(116, 116, 116); 
    border-radius: 10px;
    margin-top: 5vmin;
    -webkit-box-shadow: 8px 3px 29px -2px rgba(0,0,0,0.75);
    -moz-box-shadow: 8px 3px 29px -2px rgba(0,0,0,0.75);
    box-shadow: 8px 3px 29px -2px rgba(0,0,0,0.75);
}


